/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #002222;
  margin: 0;
}

p {
  font-size: 1.2rem;
}

.container {
  width: 100%;
  max-width: 140rem;
  margin: 0 auto;
  display: flex;
}

a {
  font-family: "Bebas Neue", cursive;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
}

h1,
h2,
h3 {
  font-family: "Lato", sans-serif;
  margin: 10em;
  color: black;
  margin: 0;
  font-weight: 500;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.8rem;
  color: rgb(135, 150, 22);
}

h4 {
  font-size: 2rem;
  margin: 0;
}

.bg-img {
  max-width: 100%;
  height: 100vh;
}

.item-unvisible {
  visibility: hidden;
}
@media (min-width: 1007px) {
  .item-unvisible {
    visibility: visible;
  }
}

.item-visible {
  visibility: visible;
}

header {
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 101;
  overflow: hidden;
}

header .container {
  position: relative;
  padding: 2rem;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 100%;
}

.navbar-logo {
  animation: navbar-logo 2s cubic-bezier(0.5, 0.6, 0.6, 1);
  animation-fill-mode: backwards;
}
.navbar-logo h4 {
  font-family: "Bebas Neue", cursive;
  font-size: 10rem;
  color: #7f1a35;
  transition: cubic-bezier(0.8, 1, 0.8, 1) 1s;
}
.navbar-logo h4 span {
  color: #FFF0D3;
}

@keyframes navbar-logo {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.navbar-menu {
  animation: navbar-menu 2s cubic-bezier(1, 0.6, 0.6, 0.5);
  animation-fill-mode: backwards;
  animation-delay: 1s;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 3rem !important;
}
.navbar-menu a {
  color: #FFF0D3;
  padding: 0rem 2rem;
  font-size: 2.5rem;
}
@media (min-width: 1007px) {
  .navbar-menu {
    position: fixed;
    top: 85vh;
    flex-direction: row;
    justify-content: center;
  }
  .navbar-menu a {
    padding: 0.45rem 2.5rem 0 2.5rem;
    font-size: 5vw;
    transition: cubic-bezier(0.8, 1, 0.8, 1) 0.8s;
  }
  .navbar-menu .active {
    background-color: #005555;
  }
  .navbar-menu a:hover {
    background-color: rgba(0, 0, 0, 0.6);
  }
}

@keyframes navbar-menu {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.move-header {
  padding: 0 !important;
}

.bg-header {
  background-color: #002222;
}

.move-logo h4 {
  font-size: 5rem;
  transition: cubic-bezier(0.5, 0.3, 0.3, 0.8) 1.4s;
}

.move-menu {
  top: 90%;
  transition: cubic-bezier(0.8, 1, 0.8, 1) 1.5s;
}
.move-menu a {
  font-size: 2vw;
}

.change-color a {
  color: #7f1a35;
}
.change-color .active {
  color: #FFF0D3;
  background-color: #7f1a35;
}

section {
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
}

.personal-img {
  z-index: 100;
  position: fixed;
  top: 15%;
  left: 10%;
  right: 0;
  bottom: 0;
  background-image: url(../../../src/img/test2.png);
  background-size: cover;
  background-repeat: no-repeat;
  animation: personal-img 1.2s cubic-bezier(0.5, 0.6, 0.6, 1);
  animation-fill-mode: backwards;
}

@keyframes personal-img {
  0% {
    opacity: 0;
    transform: scale(1.1);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
